.aboutHeader {
  display: flex;
  justify-content: space-evenly;
  gap: 25px;
}

@media only screen and (max-width: 720px) {
  .aboutHeader {
    flex-direction: column;
    align-items: center;
  }
}

@media only screen and (max-width: 480px) {
  .aboutHeader {
    align-items: flex-start;
  }
}
